@import url("https://fonts.googleapis.com/css2?family=Outfit:wght@300;400;600&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Outfit", sans-serif;
  background-color: #0d192b;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  color: white;
}

p {
  font-size: 18px;
  font-weight: 300;
}

img {
  max-width: 100%;
}

main {
  box-shadow: #060c14 0 0 20px;
  width: 340px;
  background-color: #14253d;
  padding: 1.5rem;
  border-radius: 3%;
  color: #8bacda;
}
main img {
  border-radius: 3%;
}
main h2 {
  color: white;
}
main h2, main p, main ul {
  margin: 1.3rem 0;
}
main ul {
  display: flex;
  justify-content: space-between;
}
main ul img {
  margin-right: 0.45rem;
}
main ul li {
  list-style: none;
}
main ul div {
  display: flex;
  align-items: center;
}

main .footer {
  border-top: 1px solid;
  display: flex;
  align-items: center;
}
main .footer img {
  width: 2.5rem;
  height: 2.5rem;
  margin-right: 1rem;
  outline: 0.15rem solid white;
  border-radius: 50%;
}

main ul div:nth-child(1) {
  color: #00fff7;
  font-weight: 600;
}

main [alt=nft-image] {
  position: relative;
}

.overlay {
  position: relative;
  width: 100%;
  height: auto;
}

.overlay:hover img:nth-child(2) {
  cursor: pointer;
  opacity: 1;
  transition: opacity 330ms;
}

.overlay img:nth-child(2) {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 42%;
  background-color: #00fff752;
  opacity: 0;
  transition: opacity 250ms;
}

main h2:hover {
  color: #00fff7;
  cursor: pointer;
}

main .footer .auth {
  font-weight: 600;
  color: white;
}

main .footer .auth:hover {
  color: #00fff7;
  cursor: pointer;
}

@media screen and (max-width: 350px) {
  main {
    transform: scale(0.85);
  }
}